  export const getfootballcontestdownload = (from, to) => {
    return async (dispatch) => {
      dispatch(getdownload())
      await http
        .get(from && to ? `/football/reports/contestReport?from=${from}&to=${to}&download=true` : '/football/reports/contestReport?download=true', { responseType: 'blob' })
        .then((response) => {
          notifySuccessadd('Succesfully Downloaded')
          const url = window.URL.createObjectURL(new Blob([response.data]))
          const link = document.createElement('a')
          link.href = url
          link.setAttribute('download', 'Football_Contest.csv') //or any other extension
          document.body.appendChild(link)
          link.click()
          dispatch({
            type: 'SET_DOWNLOAD_OFF'
          })
        })
        .catch(() => {
          dispatch({
            type: 'SET_DOWNLOAD_OFF'
          })
          notifyError('Download Failed')
        })
    }
  }
  
