import { Fragment, useEffect, useState } from 'react'
import { useDispatch, useSelector } from "react-redux"
import Select from 'react-select'
import { selectThemeColors } from '@utils'
import { Card, Row, Col, Label, Button } from 'reactstrap'
import { getAllSeries, getAllFixture, getplayerreport, collectseriesid, collectfixtureid, getfootballTotal } from "../store/actions"
import Table from "./list/Table"
// import Breadcrumbs from '@components/breadcrumbs'
import { useForm } from 'react-hook-form'
import classnames from 'classnames'
import AppCollapse from '@components/app-collapse'
import { Filter } from 'react-feather'

// ** Import Scrollbar Paginate Component
import { AsyncPaginate } from "react-select-async-paginate"

const SelectReact = () => {
  const defaultValues = {
    reactSelect: null
  }
  const { handleSubmit } = useForm({ defaultValues })
  const store = useSelector((state) => state.reports)
  const store1 = useSelector((state) => state.reports)
  const [sid, setSid] = useState(0)
  const [fid, setFid] = useState(0)
  const [val, setVal] = useState(false)
  const [table, setTable] = useState(false)
  const [refresh, setRefresh] = useState(false)
  const [serilist, setSerilist] = useState("")
  const [fixlist, setFixlist] = useState("")
  const [page, setPage] = useState(1)
  const [value] = useState(null)
  const dispatch = useDispatch()

  const fixture = store.football_fixture_name.map(function (fix) {
    return { value: fix.fixture_name, label: fix.fixture_name }
  })

  const filterid = store.football_series_name.filter(s => s.series_name === serilist)
  const filterid2 = store.football_fixture_name.filter(f => f.fixture_name === fixlist)
  console.log(store.football_fixture_name)

  useEffect(() => {
    dispatch(getfootballTotal(page))
  }, [page])

  const submit = (e) => {
    e.preventDefault()
    setVal(true)
    if (serilist === "") {
      setTable(false)
    } else {
      dispatch(getplayerreport(serilist, filterid2[0]?.id))
      dispatch(collectseriesid(serilist))
      dispatch(collectfixtureid(filterid2[0]?.id))
      console.log(filterid[0]?.id, filterid2[0]?.id)
      setTable(true)
    }
  }

  console.log(store.football_player_reports)
  console.log(serilist)
  // onChange={(e) => setPrizeform({...prizeform, category: e.value})}

  const serieslist = (e) => {    
    setSerilist(e.value)
    console.log(serilist)
    setRefresh(true)
    console.log('change')
    dispatch(getAllFixture(e.value))
    setSid(filterid[0]?.id)
    // setChange(true)
    console.log(filterid[0]?.id)
    // return store.series_name.filter(s => s.series_name === list)
  }

  const fixturelist = (e) => {
    setFixlist(e.value)
    setRefresh(false)
    setFid(filterid2[0]?.id)
    console.log(filterid2[0]?.id)
    console.log(fid)
    // return store.series_name.filter(s => s.series_name === list)
  }

  console.log(filterid[0]?.id)
  console.log(filterid2[0]?.id)
  // setId(list.filter(l => l === store.series_name.series_name))    }

  useEffect(() => {
    dispatch(getAllSeries())
    console.log(store)
  }, [])

  useEffect(() => {
    // dispatch(getAllFixture(filterid[0]?.id))
    console.log("now")
    console.log(store.football_fixture_name)
  }, [sid])


  const handleKeyPress = (e) => {
    if (e.keyCode === 13) {
      // setCurrentPage(1)      
      dispatch(getplayerreport(filterid[0]?.id, filterid2[0]?.id))
      dispatch(collectseriesid(filterid[0]?.id))
      dispatch(collectfixtureid(filterid2[0]?.id))
    }
  }

  console.log(store1)

  // ************************************************************************************* //

  const options = []
  for (let i = 0; i < store1.football_total?.data?.total; i++) {
    if (page === store1.football_total?.data?.current_page) {
      options.push({
        value: `${store1.football_total?.data?.data[i]?.id}`,
        label: `${store1.football_total?.data?.data[i]?.series_name}`
      })
    }

  }
  const sleep = ms => new Promise(resolve => { setTimeout(() => { resolve() }, ms) })
  const optionsPerPage = 10

  const loadOptions = async (search, prevOptions, page) => {
    await sleep(1000)
    if (page === store1.football_total?.data?.current_page) {
      const nextData = store1.football_total?.data?.data?.map((nextData) => { return { value: nextData.id, label: nextData.series_name } })
      const scrollLength = [...prevOptions, ...nextData]
      let filteredOptions
      if (!search) {
        filteredOptions = scrollLength.length > 10 ? scrollLength : options
      } else {
        const searchLower = search.toLowerCase()
        filteredOptions = [...scrollLength.length > 10 ? scrollLength : options].filter(({ label }) => label.toLowerCase().includes(searchLower)
        )
      }

      const hasMore = Math.ceil(store1.football_total?.data?.total / optionsPerPage) > page
      const slicedOptions = filteredOptions.slice(
        (page - 1) * optionsPerPage,
        page * optionsPerPage
      )

      return {
        options: slicedOptions,
        hasMore
      }
    }
  }
  const defaultAdditional = {
    page: 1
  }

  const loadPageOptions = async (q, prevOptions, { page }) => {
    setPage(page)
    const { options, hasMore } = await loadOptions(q, prevOptions, page)
    return {
      options: [...options],
      hasMore,

      additional: {
        page: page + 1
      }
    }
  }

  // ************************************************************************************* //

  const data = [
    {
      title: <div><Filter size={18} /> Filter...</div>,
      content: (
        <>
          <Row>
            <Col className='mb-1' md='6' sm='12'>
              <Label>series</Label>
              <AsyncPaginate
                menuPortalTarget={document.querySelector('body')}
                theme={selectThemeColors}
                className={classnames('react-select', { 'is-invalid': serilist === "" && val })}
                classNamePrefix='select'
                additional={defaultAdditional}
                value={value?.label}
                loadOptions={loadPageOptions}
                onChange={(e) => serieslist(e)}
                onKeyDown={(e) => handleKeyPress(e)}
              />
              {serilist === "" && val ? <h5 className='text-danger'>Please enter a value</h5> : ""}
            </Col>
            <Col className='mb-1' md='6' sm='12'>
              <Label>fixture</Label>
              <Select
                isDisabled={store.fixloader}
                onChange={(e) => fixturelist(e)}
                theme={selectThemeColors}
                className='react-select'
                value={refresh ? " " : fid}
                classNamePrefix='select'
                options={fixture ? fixture : ""}
                isClearable={true}
              />
            </Col>
          </Row>
          <Button.Ripple
            color='primary'
            onClick={(e) => handleSubmit(submit(e))}
          >submit</Button.Ripple>
        </>
      )
    }
  ]

  return (
    <Fragment>
      {/* <Breadcrumbs breadCrumbTitle='Football Report' breadCrumbActive='Playerreport Report' /> */}
      <AppCollapse data={data} type='border' accordion className='mb-1' />
      <Card>

        {table ? <Table /> : ""
        }
      </Card>
    </Fragment>
  )
}
export default SelectReact